import { DataStore } from './../../models/data-store';
import { VeiculoService } from './../../providers/api/veiculo.service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the SensorPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sensor',
  templateUrl: 'sensor.html',
})
export class SensorPage {
  
  sensores:any[];
  veiculo:DataStore;
  constructor(public navCtrl: NavController, public navParams: NavParams, public veiculoService: VeiculoService) {
    this.sensores = this.navParams.get("sensores");
    this.veiculo = this.navParams.get("veiculo");
  }

  ionViewDidLoad() {
  }

  

  doRefresh(refresher) {
    this.veiculoService
      .getStatusVeiculo(this.veiculo.veiculoId)
      .subscribe(
        (res: any) => {
          this.sensores = res
        },
        error => {
          refresher.complete();
        },
        () => {
          refresher.complete();
        }
      );
  }

}



